import React from 'react';
import Box from '@mui/material/Box';
import AspectRatio from '@mui/joy/AspectRatio';
import Card from '@mui/joy/Card';
import CardContent from '@mui/joy/CardContent';
import CardOverflow from '@mui/joy/CardOverflow';
import Divider from '@mui/joy/Divider';
import Typography from '@mui/joy/Typography';
import { useThemeContext } from './../Burger/themeContext';
import themes from './../Burger/theme';
import STMicroelectronics from './../../img/ST.png';
import Harvest from './../../img/Harvest.JPG'

function About() {
  const {darkMode} = useThemeContext();

  return (
    <Box
      component="main"
      sx={{
        flexGrow: 1,
        p: 3,
        height: '100vh',
        overflow: 'auto',
        backgroundColor: darkMode? themes.dark.backgroundColor : themes.light.backgroundColor,
        minWidth: '100%'
      }}
    >
        <h1>EXPERIENCE PROFESSIONNELLE</h1><br></br><br></br><br></br><br></br><br></br>

        <Box style={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'space-evenly' }}>

        <Card variant="outlined" sx={{ width: 400, margin: '20px',
            borderRadius: 3,
            transition: '0.2s',
            '&:hover': { transform: 'scale(1.05)' }, bgcolor: darkMode? themes.dark.backgroundColor : themes.light.backgroundColor}}>
      <CardOverflow>
        <AspectRatio ratio="2">
        <img src={STMicroelectronics} alt="STMicroelectronics" style={{ width: '100%', height: '100%', objectFit: 'cover' }} />
        </AspectRatio>
      </CardOverflow>
      <CardContent>
        <Typography level="title-lg" sx={{color: darkMode? themes.dark.text : themes.light.textColor}}>Alternance chez STMicroelectronics</Typography><br/>
        <Typography level="title-md" sx={{color: darkMode? themes.dark.text : themes.light.textColor}}>Dveloppement d'une application Typescript avec React et Node</Typography><br/>
        <Typography level="body-sm" sx={{color: darkMode? themes.dark.primaryColor : themes.light.primaryColor}}>2023 - 2024</Typography>
      </CardContent>
      <CardOverflow variant="soft" sx={{ bgcolor: darkMode? themes.dark.backgroundColor : themes.light.backgroundColor }}>
        <Divider inset="context" />
        <CardContent orientation="horizontal">
          <Typography level="body-xs" fontWeight="md" textColor="text.secondary" sx={{color: darkMode? themes.dark.text : themes.light.textColor}}>
            6.3k views
          </Typography>
          <Divider orientation="vertical" />
          <Typography level="body-xs" fontWeight="md" textColor="text.secondary" sx={{color: darkMode? themes.dark.text : themes.light.textColor}}>
            1 hour ago
          </Typography>
        </CardContent>
      </CardOverflow>
    </Card>


    <Card variant="outlined" sx={{ width: 400, margin: '20px',
            borderRadius: 3,
            transition: '0.2s',
            '&:hover': { transform: 'scale(1.05)' }, bgcolor: darkMode? themes.dark.backgroundColor : themes.light.backgroundColor}}>
      <CardOverflow>
        <AspectRatio ratio="2">
        <img src={Harvest} alt="STMicroelectronics" style={{ width: '100%', height: '100%', objectFit: 'cover' }} />
        </AspectRatio>
      </CardOverflow>
      <CardContent>
        <Typography level="title-lg" sx={{color: darkMode? themes.dark.text : themes.light.textColor}}>Stage chez Harvest</Typography><br/>
        <Typography level="title-md" sx={{color: darkMode? themes.dark.text : themes.light.textColor}}>Internationalisation d'une application Angular/Java</Typography><br/>
        <Typography level="body-sm" sx={{color: darkMode? themes.dark.primaryColor : themes.light.primaryColor}}>07/05/2023 - 07/07/2023</Typography>
      </CardContent>
      <CardOverflow variant="soft" sx={{ bgcolor: darkMode? themes.dark.backgroundColor : themes.light.backgroundColor }}>
        <Divider inset="context" />
        <CardContent orientation="horizontal">
          <Typography level="body-xs" fontWeight="md" textColor="text.secondary" sx={{color: darkMode? themes.dark.text : themes.light.textColor}}>
            6.3k views
          </Typography>
          <Divider orientation="vertical" />
          <Typography level="body-xs" fontWeight="md" textColor="text.secondary" sx={{color: darkMode? themes.dark.text : themes.light.textColor}}>
            1 hour ago
          </Typography>
        </CardContent>
      </CardOverflow>
    </Card>
    </Box>
    </Box>
  );
};

export default About;
