import React from 'react';
import Box from '@mui/material/Box';
import MediaCard from './card';
import { useThemeContext } from './../Burger/themeContext';
import themes from './../Burger/theme'

function MesProjets () {
  const {darkMode} = useThemeContext();

  // Calculer la largeur en soustrayant la taille du menu
  //const contentWidth = `calc(100% - 240px)`; // Remplacez 240px par la largeur relle de votre menu

  return (
    <Box
      component="main"
      sx={{
        flexGrow: 1,
        p: 3,
        overflow: 'auto',
        backgroundColor: darkMode? themes.dark.backgroundColor : themes.light.backgroundColor,
        color: darkMode? themes.dark.text : themes.light.textColor,
        minWidth: '100%'
      }}
    >
        <h1>MES PROJETS</h1>
        <br></br>
        <MediaCard></MediaCard>
    </Box>
  );
};

export default MesProjets;
