import Select from 'react-select';
import React from 'react';
import i18n from '../../i18n/i18n';
import MenuItem from '@mui/material/MenuItem';
import { useThemeContext } from './themeContext';
import themes from './theme';

const customStyles = {
  control: (provided, state) => ({
    ...provided,
   backgroundColor: themes.dark.backgroundColor,
    width: '100px', // Rduire la largeur du contrle
    minHeight: '30px', // Rduire la hauteur du contrle
    border: 'none',
  }),
  menuList: (provided, state) => ({
    ...provided,
    color: themes.dark.text,
    backgroundColor: themes.dark.backgroundColor,
    '& div': {
      '&:hover': {
        color: themes.dark.primaryColor, // Couleur du hover sur les choix
      },
    },
  }),
  option: (provided, state) => ({
    ...provided,
    backgroundColor: themes.dark.backgroundColor,
    width: '100px', // Rduire la largeur du contrle
    minHeight: '30px', // Rduire la hauteur du contrle
  }),
  singleValue: (provided, state) => ({
    ...provided,
    color: themes.dark.text,
  }),
};

const customStylesWhite = {
  control: (provided, state) => ({
    ...provided,
    backgroundColor: 'white',
    width: '100px', // Rduire la largeur du contrle
    minHeight: '30px', // Rduire la hauteur du contrle
    border: 'none',
  }),
  menuList: (provided, state) => ({
    ...provided,
    '& div': {
      '&:hover': {
        backgroundColor: 'grey', // Couleur du hover sur les choix
      },
      color: 'black',
    },
  }),
  option: (provided, state) => ({
    ...provided,
    backgroundColor: 'white',
    width: '100px', // Rduire la largeur du contrle
    minHeight: '30px', // Rduire la hauteur du contrle
  }),
  singleValue: (provided, state) => ({
    ...provided,
    color: 'black',
    backgroundColor: 'white',
  }),
};


const options = [
  { value: 'fr', label: 'FR' },
  { value: 'en', label: 'EN' },
  { value: 'it', label: 'IT' },
];

function SelectLang() {
  const [value, setValue] = React.useState(options[0]); // Dfinir la valeur par dfaut sur la premire option (FR)
  const {darkMode} = useThemeContext();

  const handleChange = (selectedOption) => {
    i18n.changeLanguage(selectedOption.value);
    setValue(selectedOption);
  };

  return (
    <Select
      options={options}
      value={value}
      onChange={handleChange}
      styles={darkMode ? customStyles : customStylesWhite}
      isSearchable={false} // Dsactiver la recherche
      >
        {options.map((option) => (
        <MenuItem key={option.value} value={option.value}>
          {option.label}
        </MenuItem>
      ))}
    </Select>
  );
}

export default SelectLang;
