import * as React from 'react';
import PropTypes from 'prop-types';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import CssBaseline from '@mui/material/CssBaseline';
import Drawer from '@mui/material/Drawer';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import Typography from '@mui/material/Typography';
import { grey } from '@mui/material/colors';
import ContactMailIcon from '@mui/icons-material/ContactMail';
//import CodeIcon from '@mui/icons-material/Code';
import DesktopWindowsSharpIcon from '@mui/icons-material/DesktopWindowsSharp';
import { Link } from 'react-router-dom';
import './drawer.css';
import SelectLang from './selectLang';
import { useTranslation } from 'react-i18next';
import SchoolIcon from '@mui/icons-material/School';
import HomeIcon from '@mui/icons-material/Home';
import SwitchBut from './mode';
import IconButton from '@mui/material/IconButton';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import Avatar from './avatar';
import MenuIcon from '@mui/icons-material/Menu';
import Stack from '@mui/material/Stack';
import { useThemeContext } from './themeContext';
import { useLocation } from 'react-router-dom';
import themes from './theme';

const drawerWidth = 240;

function ResponsiveDrawer(props) {
  const { t } = useTranslation();
  const { window } = props;
  const [mobileOpen, setMobileOpen] = React.useState(false);
  const [isClosing, setIsClosing] = React.useState(false);
  const {darkMode} = useThemeContext();
  const location = useLocation();

  const handleDrawerClose = () => {
    setIsClosing(true);
    setMobileOpen(false);
  };

  const handleDrawerTransitionEnd = () => {
    setIsClosing(false);
  };

  const handleDrawerToggle = () => {
    if (!isClosing) {
      setMobileOpen(!mobileOpen);
    }
  };

  const drawer = (
    <div sx={{ display: 'flex', flexDirection: 'column'}} className="drawer-container">
      <List
        sx={{
          display: 'flex',
          justifyContent: 'space-around'
        }}
      >
        <SwitchBut />
        <SelectLang />
      </List>
      <List
      sx={{
        color : darkMode? themes.dark.text : themes.light.textColor,
      }}>
        <Stack sx={{marginTop: '10%'}}>
        <Avatar />
        </Stack> 
        <Typography variant="h4" gutterBottom sx={{textAlign: 'left', marginLeft: '20px', marginTop: '10%', marginBottom:'15px', fontFamily: 'inherit'}}> 
        Louise Sanchez
      </Typography>
      </List>
      <List sx={{ color : darkMode? themes.dark.text : themes.light.textColor }}>
      <Stack
      sx={{
        marginTop: '10%',
        height: '51vh',
        display: 'flex',
        justifyContent: 'space-between',
      }}>
        <Stack>
        {[
          { text: t('ACCUEIL'), icon: <HomeIcon sx={{'&:hover': { color: darkMode ?themes.dark.primaryColor : themes.light.primaryColor} }} />, path: '/' },
          { text: t('PROJETS'), icon: </*CodeIcon*/ DesktopWindowsSharpIcon sx={{'&:hover': { color: darkMode ?themes.dark.primaryColor : themes.light.primaryColor} }} />, path: '/mesProjets' },
          { text: t('FOMATION'), icon: <SchoolIcon sx={{'&:hover': { color: darkMode ?themes.dark.primaryColor : themes.light.primaryColor} }} />, path: '/formation' },
          { text: t('A_PROPOS'), icon: <AccountCircleIcon sx={{'&:hover': { color: darkMode ?themes.dark.primaryColor : themes.light.primaryColor} }} />, path: '/apropos' },
      ].map((item) => (
      <ListItem key={item.text} disablePadding sx={{ marginBottom: '9px'}}>
          <ListItemButton component={Link} to={item.path} onClick={handleDrawerToggle} sx={{'&:hover': {
      color: darkMode? themes.dark.primaryColor : themes.light.primaryColor},
        ...(location.pathname === item.path && {
          color: themes.dark.primaryColor }) }}>
          <ListItemIcon sx={{ color: location.pathname === item.path ? (darkMode ? themes.dark.primaryColor : themes.light.primaryColor) : grey[500] }}>{item.icon}</ListItemIcon>
          <ListItemText primary={item.text} />
          </ListItemButton>
      </ListItem>
      ))}
      </Stack>
      <Stack>
          {[{ text: t('CONTACT'), icon: <ContactMailIcon />, path: '/contact' }].map((item) => (
          <ListItem key={item.text} disablePadding>
            <ListItemButton component={Link} to={item.path} sx={{'&:hover': {
      color: darkMode? themes.dark.primaryColor : themes.light.primaryColor},
        ...(location.pathname === item.path && {
          color: darkMode? themes.dark.primaryColor : themes.light.primaryColor}) }}>
            <ListItemIcon sx={{ color: location.pathname === item.path ? (darkMode ? themes.dark.primaryColor : themes.light.primaryColor) : grey[500] }}>{item.icon}</ListItemIcon>
            <ListItemText primary={item.text} />
            </ListItemButton>
        </ListItem>
          ))}
        </Stack>
      </Stack>
      </List>
    </div>
  );

  const container = window !== undefined ? () => window().document.body : undefined;

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', background: darkMode ? themes.dark.backgroundColor : themes.light.backgroundColor}} className="drawer-container">
      <CssBaseline />
      <AppBar
        position="fixed"
        sx={{
          width: { sm: `calc(100% - ${drawerWidth}px)` },
          ml: { sm: `${drawerWidth}px` },
        }}
      >
      </AppBar>
      <Box sx={{ display: 'flex', justifyContent: 'flex-start', alignItems: 'flex-start'}}>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            sx={{ mr: 2, display: { sm: 'none' }, ml:'10px' }}
          >
            <MenuIcon />
          </IconButton>
        </Box>
      <Box 
        component="nav"
        sx={{
            width: { sm: drawerWidth },
            flexShrink: { sm: 0 },
          }}
        aria-label="mailbox folders"
      >
        <Drawer
          container={container}
          variant="temporary"
          open={mobileOpen}
          onTransitionEnd={handleDrawerTransitionEnd}
          onClose={handleDrawerClose}
          ModalProps={{
            keepMounted: true, 
          }}
          sx={{
            display: { xs: 'block', sm: 'none' },
            '& .MuiDrawer-paper': { boxSizing: 'border-box', width: drawerWidth, backgroundColor: darkMode? themes.dark.backgroundColor : themes.light.backgroundColor}}}
        >
          {drawer}
        </Drawer>
        <Drawer
          variant="permanent"
          sx={{
            display: { xs: 'none', sm: 'block' },
            '& .MuiDrawer-paper': { boxSizing: 'border-box', width: drawerWidth, backgroundColor: darkMode? themes.dark.backgroundColor : themes.light.backgroundColor},
          }}
          open
        >
          {drawer}
        </Drawer>
      </Box>
    </Box>
  );
}

ResponsiveDrawer.propTypes = {
  /**
   * Injected by the documentation to work in an iframe.
   * You won't need it on your project.
   */
  window: PropTypes.func,
};

export default ResponsiveDrawer;
