import * as React from 'react';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import List from '@mui/material/List';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import DialogActions from '@mui/material/DialogActions';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import { styled } from '@mui/material/styles';
import { useThemeContext } from './../Burger/themeContext';
import themes from './../Burger/theme'
import spaceSquad from './../../img/spacesquad1PNG.PNG';
import portfolioCouverture from './../../img/portefolio1.JPG';
import reactCouverture from './../../img/reactapp1.JPG';
import pythonCouverture from './../../img/python1PNG.png';
import javaCouverture from './../../img/javarpg.jpg';

const cardData = [
  {
    title: 'React / Node Application',
    image: reactCouverture,
    description: 'Lizards are a widespread group of squamate reptiles, with over 6,000 species, ranging across all continents except Antarctica',
  },
  {
    title: 'Web Site',
    image: spaceSquad,
    description: 'Lizards are a widespread group of squamate reptiles, with over 6,000 species, ranging across all continents except Antarctica',
  },
  {
    title: 'Portfolio premire version',
    image: portfolioCouverture,
    description: 'Lizards are a widespread group of squamate reptiles, with over 6,000 species, ranging across all continents except Antarctica',
  },
  {
    title: 'Python casse-brique',
    image: pythonCouverture,
    description: 'Lizards are a widespread group of squamate reptiles, with over 6,000 species, ranging across all continents except Antarctica',
  },
  {
    title: 'Java RPG',
    image: javaCouverture,
    description: 'Lizards are a widespread group of squamate reptiles, with over 6,000 species, ranging across all continents except Antarctica',
  },
];

const CardsContainer = ({ darkMode, themes, handleClickOpen }) => {
  return (
    <div style={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'normal' }}>
      {cardData.map((data, index) => (
        <Card
          key={index}
          onClick={handleClickOpen}
          sx={{
            maxWidth: 345,
            margin: '20px',
            borderRadius: 2,
            boxShadow: ` 0px 0px 15px 0px rgba(143, 7, 255, 0.4)`,
            transition: '0.2s',
            '&:hover': { transform: 'scale(1.05)' },
          }}
        >
          <CardMedia sx={{ height: 140 }} image={data.image} title={data.title} />
          <CardContent sx={{ backgroundColor: darkMode ? themes.dark.cardBackgroundColor : themes.light.cardBackgroundColor }}>
            <Typography gutterBottom variant="h5" component="div" sx={{ color: themes.dark.text }}>
              {data.title}
            </Typography>
            <Typography variant="body2" sx={{ color : darkMode? themes.dark.text : themes.light.textColor}}>
              {data.description}
            </Typography>
          </CardContent>
          <CardActions sx={{ backgroundColor: darkMode ? themes.dark.cardBarBackgroundColor : themes.light.cardBarBackgroundColor }}>
            <Button size="small" sx={{ color: themes.dark.primaryColor }}>
              Share
            </Button>
            <Button size="small" sx={{ color: themes.dark.primaryColor }}>
              Learn More
            </Button>
          </CardActions>
        </Card>
      ))}
    </div>
  );
};

const BootstrapDialog = styled(Dialog)(({ theme }) => ({
  '& .MuiDialog-paper': {
    backgroundColor: 'white', // Couleur de fond de la pop-up
  },
  '& .MuiDialogContent-root': {
    padding: theme.spacing(2),
  },
  '& .MuiDialogActions-root': {
    padding: theme.spacing(1),
  },
}));
function MediaCard() {
  const [open, setOpen] = React.useState(false);
  const {darkMode} = useThemeContext();

  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };

    return (
      <React.Fragment>
        <List>
          <CardsContainer darkMode={darkMode} themes={themes} handleClickOpen={handleClickOpen}/>
            {/* {reactProject}
            {Java}
            {Python}
            {Internationalisation} */}
        </List>
        <BootstrapDialog
        onClose={handleClose}
        aria-labelledby="customized-dialog-title"
        open={open}
      >
        <DialogTitle sx={{ m: 0, p: 2 }} id="customized-dialog-title">
          Modal title
        </DialogTitle>
        <IconButton
          aria-label="close"
          onClick={handleClose}
          sx={{
            position: 'absolute',
            right: 8,
            top: 8,
            color: (theme) => theme.palette.grey[500],
          }}
        >
          <CloseIcon />
        </IconButton>
        <DialogContent dividers>
          <Typography gutterBottom>
            Cras mattis consectetur purus sit amet fermentum. Cras justo odio,
            dapibus ac facilisis in, egestas eget quam. Morbi leo risus, porta ac
            consectetur ac, vestibulum at eros.
          </Typography>
          <Typography gutterBottom>
            Praesent commodo cursus magna, vel scelerisque nisl consectetur et.
            Vivamus sagittis lacus vel augue laoreet rutrum faucibus dolor auctor.
          </Typography>
          <Typography gutterBottom>
            Aenean lacinia bibendum nulla sed consectetur. Praesent commodo cursus
            magna, vel scelerisque nisl consectetur et. Donec sed odio dui. Donec
            ullamcorper nulla non metus auctor fringilla.
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={handleClose}>
            Save changes
          </Button>
        </DialogActions>
      </BootstrapDialog>
        </React.Fragment>
    );
};

export default MediaCard;
